import * as actions from "./actionsTypes";
import axios from "axios";
import axios_orders from "../../axios-orders";

export const authStart = () => {
  return {
    type: actions.AUTH_START,
  };
};
export const authSuccess = (idToken, localId) => {
  return {
    type: actions.AUTH_SUCCESS,
    idToken,
    localId,
  };
};
export const authFail = (error) => {
  return {
    type: actions.AUTH_FAIL,
    error,
  };
};

export const auth = (email, password, isSignUp) => {
  return (dispatch) => {
    const authData = {
      email,
      password,
      returnSecureToke: true,
    };
    let url =
      "https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyB2Y7x_VPQjEuqssyDyyBc3wqJdNuilWPQ";
    if (!isSignUp)
      url =
        "https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyB2Y7x_VPQjEuqssyDyyBc3wqJdNuilWPQ";
    axios
      .post(url, authData)
      .then((response) => {
        if (isSignUp) {
          const expirationTime = new Date(
            new Date().getTime() + response.data.expiresIn * 1000
          );
          localStorage.setItem("token", response.data.idToken);
          localStorage.setItem("expirationDate", expirationTime);
          localStorage.setItem("userId", response.data.localId);
          dispatch(authSuccess(response.data.idToken, response.data.localId));
          dispatch(checkAuthTimeout(response.data.expiresIn));
          axios_orders.post("users.json", {
            userId: response.data.localId,
            refreshToken: response.data.refreshToken,
          });
        } else {
          dispatch(getRefreshToken(response.data.localId));
        }
      })
      .catch((error) => {
        dispatch(authFail(error.response.data.error));
      });
    dispatch(authStart());
  };
};

export const logout = () => {
  localStorage.removeItem("token");
  localStorage.removeItem("expirationDate");
  localStorage.removeItem("userId");
  return {
    type: actions.AUTH_LOGOUT,
  };
};
export const checkAuthTimeout = (expirationTime) => {
  return (dispatch) => {
    setTimeout(() => {
      dispatch(logout());
    }, expirationTime * 1000);
  };
};
export const authCheckState = () => {
  return (dispatch) => {
    const idToken = localStorage.getItem("token");

    if (!idToken) {
      dispatch(logout()); //NOT REQUIRED
    } else {
      const expirationTime = new Date(localStorage.getItem("expirationDate"));

      if (expirationTime <= new Date()) {
        dispatch(logout());
      } else {
        const localId = localStorage.getItem("userId");
        dispatch(authSuccess(idToken, localId));
        dispatch(
          checkAuthTimeout(
            (expirationTime.getTime() - new Date().getTime()) / 1000
          )
        );
      }
    }
  };
};

export const getRefreshToken = (userId) => {
  return (dispatch) => {
    const queryParams = '?orderBy="userId"&equalTo="' + userId + '"';
    axios_orders.get("/users.json" + queryParams).then((response) => {
      let Token = null;
      for (let key in response.data) Token = response.data[key].refreshToken;
      dispatch(refreshToken(Token));
    });
  };
};

export const refreshToken = (refreshToken) => {
  return (dispatch) => {
    axios
      .post(
        "https://securetoken.googleapis.com/v1/token?key=AIzaSyB2Y7x_VPQjEuqssyDyyBc3wqJdNuilWPQ",
        "grant_type=refresh_token&refresh_token=" + refreshToken
      )
      .then((response) => {
        const expirationTime = new Date(
          new Date().getTime() + response.data.expires_in * 1000
        );

        localStorage.setItem("token", response.data.id_token);
        localStorage.setItem("expirationDate", expirationTime);
        localStorage.setItem("userId", response.data.user_id);
        dispatch(authSuccess(response.data.id_token, response.data.user_id));
        dispatch(checkAuthTimeout(response.data.expires_in));
      });
  };
};

export const setAuthRedirect = (path) => {
  return {
    type: actions.SET_AUTH_REDIRECT_PATH,
    path: path,
  };
};
